// MUI imports
import Divider from "@mui/material/Divider";
import Container from "@mui/material/Container";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import Grid from "@mui/material/Unstable_Grid2";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
// Components imports
import Todo from "./Todo";
// Hooks imports
import { useState, useContext, useEffect } from "react";
import { todoContext } from "../contexts/PostContext";
import { checkContext } from "../contexts/checkContext";
// Other npm imports [uniqueid]
import { v4 as uuidv4 } from "uuid";
export default function TodoList() {
  let [title, setTitle] = useState("");
  let [allTodosType, setAllTodosType] = useState("all");
  let { allTodos, setAllTodos } = useContext(todoContext);
  // EVENT HANDLERS
  function handleCheck(todoId) {
    const todoMap = allTodos.map((todo) => {
      if (todo.id === todoId) {
        todo.isCompleted = !todo.isCompleted;
      }
      return todo;
    });
    setAllTodos(todoMap);
    window.localStorage.setItem("todos", JSON.stringify(todoMap));
  }
  let todosRender;
  if (allTodosType === "completed") {
    todosRender = allTodos.filter((todo) => todo.isCompleted);
  } else if (allTodosType === "notCompleted") {
    todosRender = allTodos.filter((todo) => !todo.isCompleted);
  } else {
    todosRender = allTodos;
  }
  const todoJSX = todosRender.map((todo) => (
    <checkContext.Provider key={todo.id} value={{ handleCheck, todo }}>
      <Todo />
    </checkContext.Provider>
  ));

  function handleAddClick() {
    let newTodo = {
      id: uuidv4(),
      title: title,
      description: "",
      isCompleted: false,
    };
    let updatedTodos = [...allTodos, newTodo];
    setAllTodos(updatedTodos);
    window.localStorage.setItem("todos", JSON.stringify(updatedTodos));
    setTitle("");
  }

  useEffect(() => {
    console.log("testing use effect");
    if (window.localStorage.getItem("todos")) {
      const localTodos = JSON.parse(window.localStorage.getItem("todos"));
      setAllTodos(localTodos);
    }
  }, [setAllTodos]);

  function changeTodoType(type) {
    setAllTodosType(type);
  }
  // ==|| EVENT HANDLERS ||==
  // const completedTodos = allTodos.filter((t)=>{
  //   return t.isCompleted;
  // })
  return (
    <Container maxWidth="sm">
      <Card sx={{ minWidth: 275 }}>
        <CardContent>
          <Typography variant="h3" color="#0c0c0c">
            
          </Typography>
          <Divider style={{ marginBlock: "20px" }} />

          {/* ==== Start ToggleButton ==== */}
          <ToggleButtonGroup
            color="primary"
            style={{ direction: "ltr" }}
            value={allTodosType}
            exclusive
            // onChange={handleChange}
            aria-label="Platform"
          >
            <ToggleButton
              value="notCompleted"
              onClick={(e) => changeTodoType(e.target.value)}
            >
               
            </ToggleButton>
            <ToggleButton
              value="completed"
              onClick={(e) => {
                changeTodoType(e.target.value);
              }}
            >
              
            </ToggleButton>
            <ToggleButton
              value="all"
              onClick={(e) => {
                changeTodoType(e.target.value);
              }}
            >
              
            </ToggleButton>
          </ToggleButtonGroup>
          {/* ==== End ToggleButton ==== */}
          {/* ==== Start TODOS ==== */}
          <div className="cards_container">{todoJSX}</div>
          {/* ==== End TODOS ==== */}
          {/* ==== Start ADD TODOS ==== */}
          <Grid container spacing={2}>
            <Grid xs={8}>
              <TextField
                id="outlined-basic"
                label="  "
                variant="outlined"
                fullWidth
                value={title}
                onChange={(e) => setTitle(e.target.value)}
              />
            </Grid>
            <Grid xs={4}>
              <Button
                variant="contained"
                style={{ width: "100%", height: "100%" }}
                onClick={() => handleAddClick()}
                disabled={title !== "" ? false : true}
              >
                
              </Button>
            </Grid>
          </Grid>
          {/* ==== End ADD TODOS ==== */}
        </CardContent>
      </Card>
    </Container>
  );
}
