import Card from "@mui/material/Card";
import Typography from "@mui/material/Typography";
import CardContent from "@mui/material/CardContent";
import IconButton from "@mui/material/IconButton";
import Grid from "@mui/material/Unstable_Grid2";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import CheckTodo from "./CheckTodo";
import { todoContext } from "../contexts/PostContext";

// icons imports
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import { useContext, useState } from "react";
import { checkContext } from "../contexts/checkContext";
import TextField from "@mui/material/TextField";

export default function Todo() {
  const { allTodos, setAllTodos } = useContext(todoContext);
  const { todo } = useContext(checkContext);
  const [openDelete, setOpenDelete] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);
  const [editTodo, setEditTodo] = useState({ title: todo.title, details: todo.description });
  // EVENT HANDLERS
  function handleDelete() {
    const todoFilter = allTodos.filter((t) => {
      return t.id !== todo.id;
    });
    setAllTodos(todoFilter);
    setOpenDelete(false);
    window.localStorage.setItem("todos", JSON.stringify(todoFilter));

  }
  function handleEdit() {
    const todoEdit = allTodos.map((t) => {
      if (t.id === todo.id) {
        return { ...t, title: editTodo.title, description: editTodo.details };
      }else{
        return t;
      }
    });
    setAllTodos(todoEdit);
    window.localStorage.setItem("todos", JSON.stringify(todoEdit));

    setOpenEdit(false);
  }
  function deleteAlert() {
    setOpenDelete(true);
  }
  function EditAlert() {
    setOpenEdit(true);
  }
  const handleDeleteClose = () => {
    setOpenDelete(false);
  };
  const handleEditClose = () => {
    setOpenEdit(false);
  };
  // EVENT HANDLERS
  return (
    <>
      {/* ==== Delete Dialog ==== */}
      <Dialog
        open={openDelete}
        onClose={handleDeleteClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"       "}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
                  
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDeleteClose}></Button>
          <Button onClick={handleDelete} autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
      {/* ==== Delete Dialog ==== */}
      {/* ==== Edit Dialog ==== */}
      <Dialog
        open={openEdit}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"  "}</DialogTitle>
        <DialogContent>
          <TextField
            onChange={(e) =>
              setEditTodo({ ...editTodo, title: e.target.value })
            }
            autoFocus
            margin="dense"
            id="title"
            name="title"
            label=" "
            type="text"
            fullWidth
            variant="outlined"
            value={editTodo.title}
          />
          <TextField
            onChange={(e) =>
              setEditTodo({ ...editTodo, details: e.target.value })
            }
            autoFocus
            margin="dense"
            id="dsec"
            name="dsec"
            label=" "
            type="text"
            fullWidth
            variant="outlined"
            value={editTodo.details}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEditClose}></Button>
          <Button onClick={handleEdit} autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
      {/* ==== Edit Dialog ==== */}
      <Card
        className="todoCard"
        sx={{
          minWidth: 275,
          background: "#00796b",
          color: "white",
          marginBlock: 2,
          direction: "rtl",
        }}
      >
        <CardContent>
          <Grid container spacing={2} className="addTodo">
            <Grid xs={8} textAlign="right">
              <Typography
                variant="h5"
                className={todo.isCompleted ? "del" : ""}
              >
                {todo.title}
              </Typography>
              <Typography
                variant="subtitle1"
                color="#ccc"
                className={todo.isCompleted ? "del" : ""}
              >
                {todo.description}
              </Typography>
            </Grid>
            <Grid
              xs={4}
              display="flex"
              alignItems="center"
              justifyContent="space-around"
            >
              {/* ==== Check BTN ==== */}
              <CheckTodo />
              {/* ==== Check BTN ==== */}
              {/* ==== Edit BTN ==== */}
              <IconButton
                className="iconBtn"
                onClick={EditAlert}
                style={{
                  color: "#1769aa",
                  background: "white",
                  border: "3px solid #1769aa",
                }}
              >
                <EditIcon />
              </IconButton>
              {/* ==== Edit BTN ==== */}
              {/* ==== Delete BTN ==== */}
              <IconButton
                className="iconBtn"
                onClick={deleteAlert}
                style={{
                  color: "#b23c17",
                  background: "white",
                  border: "3px solid #b23c17",
                }}
              >
                <DeleteIcon />
              </IconButton>
              {/* ==== Delete BTN ==== */}
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </>
  );
}
